import styles from './profile-page.module.css';
import { Input, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { useState, useEffect, FC, MouseEvent, ChangeEvent, SyntheticEvent, FormEvent, FocusEvent } from 'react';
import { NavLink, Redirect } from 'react-router-dom';
import { logoutUser } from '../../services/actions/logout';
import { getUser, updateUser } from '../../services/actions/user';
import { getCookie } from '../../utils/cookie';
import { TFormValues } from '../../services/types/pages-types';
import { useAppDispatch, useAppSelector } from '../../services/hooks';

export const ProfileNavigation: FC = () => {
    const dispatch = useAppDispatch();
    const token = getCookie('refreshToken');

    const logoutHandler = (e: MouseEvent<HTMLAnchorElement>) => {
        e.preventDefault();
        dispatch(logoutUser(token));
    }

    return(
        <nav className={styles.profileNavWrapper}>
            <ul className='mb-20'>
                <li><NavLink exact to='/profile' className={`text text_type_main-medium text_color_inactive pt-3 pb-3 ${styles.navigationLink}`} activeClassName={styles.activeLink}></NavLink></li>
                <li><NavLink exact to='/profile/orders' className={`text text_type_main-medium text_color_inactive pt-3 pb-3 ${styles.navigationLink}`} activeClassName={styles.activeLink}> </NavLink></li>
                <li><NavLink exact to='/logout' className={`text text_type_main-medium text_color_inactive pt-3 pb-3 ${styles.navigationLink}`} activeClassName={styles.activeLink} onClick={logoutHandler}></NavLink></li>
            </ul>
            <p className='text text_type_main-default text_color_inactive'>        </p>
        </nav>
    );
}

const EditProfileForm: FC = () => {
    const dispatch = useAppDispatch();
    const user = useAppSelector(store => store.user);
    const [formValues, setFormValues] = useState<TFormValues>({name: user.name, email: user.email, password: 'password'});
    const [isEdit, setIsEdit] = useState<boolean>(false);
    const [editElements, setEditElements] = useState<object>({});

    useEffect(() => {
        if(getCookie('accessToken')) {
            dispatch(getUser());
        }
    }, [user.name, user.email])

    useEffect(() => {
        setFormValues({name: user.name, email: user.email, password: 'password'});
    }, [user.name, user.email])

    const editHandler = (e: ChangeEvent<HTMLInputElement>) => {
        e.preventDefault();
        setIsEdit(true);
        setFormValues({...formValues, [e.target.name]: e.target.value});
        setEditElements({...editElements, [e.target.name]:e.target.value});
    }

    const cancelHandler = (e: SyntheticEvent<Element, Event>) => {
        e.preventDefault();
        setFormValues({name: user.name, email: user.email, password: 'password'});
        setEditElements({});
        setIsEdit(false);
    }
    
    const formSubmitHandler = (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        dispatch(updateUser(editElements));
        setEditElements({});
        setIsEdit(false);
    }

    const onFocusPassHandler = (e: FocusEvent<HTMLInputElement, Element>) => {
        setFormValues({...formValues, password: ''});
    }

    const onBlurPassHandler = (e: FocusEvent<HTMLInputElement, Element>) => {
        if(formValues.password == '') {
            setFormValues({...formValues, password: 'password'});
        }
    }

    return(
        <section className={styles.profileFormWrapper}>
            <form className={`${styles.profileForm} mb-20`} onSubmit={formSubmitHandler}>
                <Input
                    type={'text'}
                    placeholder={''}
                    value={formValues.name}
                    icon={'EditIcon'}
                    name={'name'}
                    error={false}
                    errorText={''}
                    onChange={editHandler}
                />
                <Input
                    type={'text'}
                    placeholder={''}
                    onChange={editHandler}
                    value={formValues.email}
                    icon={'EditIcon'}
                    name={'email'}
                    error={false}
                    errorText={''}
                />
                <Input
                    type={'password'}
                    placeholder={''}
                    onChange={editHandler}
                    value={formValues.password}
                    icon={'EditIcon'}
                    name={'password'}
                    error={false}
                    errorText={''}
                    onFocus={onFocusPassHandler}
                    onBlur={onBlurPassHandler}
                />
                {(isEdit || (!user.updateUserRequest && user.updateUserFailed)) &&
                <section className={styles.profileButtons}>
                    <Button type="primary" size="medium" onClick={cancelHandler}></Button>
                    <Button type="primary" size="medium"></Button>
                </section>
                }
            </form>
        </section>
    );
}



const ProfilePage: FC = () => {
    const isLogoutSuccess = useAppSelector(store => store.logout.logoutSuccess);
    
    if(isLogoutSuccess && getCookie('refreshToken') === undefined) {
        return (
            <Redirect to='/login' />
        );  
    } else {
        return (
            <section className={styles.profileWrapper}>
                <ProfileNavigation />
                <EditProfileForm />
            </section>
        );  
    }


}

export default ProfilePage;