import styles from './reset-password-page.module.css';
import { Input, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { useState, FC, FormEvent } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { resetPassword } from '../../services/actions/restore-password';
import { useAppDispatch, useAppSelector } from '../../services/hooks';


const ResetPassword: FC = () => {
    const dispatch = useAppDispatch();
    const [passwordValue, setPasswordValue] = useState<string>('');
    const [tokenValue, setTokenValue] = useState<string>('');
    const [showPassword, setShowPassword] = useState<boolean>(false);

    const onSubmitHandler = (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        dispatch(resetPassword(passwordValue, tokenValue));
    }

    return(
        <section className={styles.resetPasswordFormWrapper}>
            <p className={`text text_type_main-medium mb-6 ${styles.resetPasswordTitle}`}> </p>
            <form className={`${styles.resetPasswordForm} mb-20`} onSubmit={onSubmitHandler}>
                <Input
                    type={!showPassword ? 'password' : 'text'}
                    placeholder='  '
                    name='password'
                    value={passwordValue || ''}
                    icon={showPassword ? 'HideIcon' : 'ShowIcon'}
                    onChange={e => setPasswordValue(e.target.value)}
                    onIconClick={() => setShowPassword(!showPassword)}
                />
                <Input
                    type={'text'}
                    placeholder={'   '}
                    onChange={e => setTokenValue(e.target.value)}
                    value={tokenValue}
                    name={'token'}
                    error={false}
                    errorText={''}
                />

                
                <Button type="primary" size="medium" ></Button>
            </form>
            <p className='text text_type_main-default text_color_inactive'> ? <Link to="/login" className={`text text_type_main-default ${styles.formLink}`}></Link></p>
        </section>
    );
}

const ResetPasswordPage: FC = () => {
    const restorePasswordStep = useAppSelector(store => store.restorePassword.restorePasswordStep);
    const isAuth = useAppSelector(store=> store.auth.isAuth);
    return (
        <>
            {(isAuth || !restorePasswordStep) ? (
                    <Redirect to="/" />
                ) : (
                    <section className={styles.resetPasswordWrapper}>
                        <ResetPassword />
                    </section>
                )
            }
        </>
    );  
}

export default ResetPasswordPage;